import axios, { AxiosRequestConfig } from 'axios';

import { message } from 'antd';
import { useThrottle } from 'ahooks';

// const env = process.env.ENV ?? 'development';
// export const isMock = !!process?.env?.MOCK;
const isMock = false;
const baseURL = isMock ? '' : '/api';

// console.log('isMock', isMock, process?.env?.MOCK, 'process', process?.env);
export const baseURlMerge = (url: string) => {
  return baseURL + url;
};
const headers: any = {
  'Content-Type': 'application/json',
};

const instance = axios.create({
  headers,
  timeout: 0,
  baseURL,
});
const history = {
  push: (url: string) => {
    window.location.href = url;
  },
};
instance.interceptors.request.use(
  (config) => {
    const headers = config.headers;
    // Ê∑ªÂä† token ËØ∑Ê±ÇÂ§¥
    const token = 'getUserinfo().token';
    token && (headers['Authorization'] = token);

    return config;
  },
  (error) => {
    console.groupCollapsed('request error');
    console.error(error);
    console.groupEnd();
    message.error(error.msg || 'ËØ∑Ê±ÇÈîôËØØ,ËØ∑ÈáçËØï');
    return Promise.reject(error);
  },
);
instance.interceptors.response.use(
  (response) => {
    const {
      config: { url },
    } = response;
    const { data } = response;
    console.groupCollapsed(`res: ${url}`);
    console.error('data', data);
    console.groupEnd();
    //Ê≤°ÊúâcodeÊàñcodeÈîôËØØ
    if (data.code !== 0 && data.code !== 1) {
      if (data.code) {
        message.error(data.msg || 'ÊúçÂä°Á´ØÈîôËØØ,ËØ∑ÈáçËØï');
        throw data.code;
      }
    }
    return data;
  },
  (error) => {
    if (!isNaN(error)) {
      Promise.reject(error);
    }
    console.groupCollapsed('response error');
    console.log(error);
    const { response } = error;
    console.log('response', response);

    if (response?.status == 401) {
      message.error('ËØ∑ÈáçÊñ∞ÁôªÂΩïÔºÅ');
      useThrottle(
        () => {
          history.push('/login');
        },
        { wait: 300 },
      );
    } else if (response?.status === 403) {
      message.error('Ê≤°ÊúâÊùÉÈôê,Á¶ÅÊ≠¢ËØ∑Ê±Ç');
    } else {
      message.error(error.msg || 'ÁΩëÁªúÈîôËØØ,ËØ∑Á®çÂêéÂÜçËØï');
    }
    console.groupEnd();
    const errCode = response?.status;
    return Promise.reject(errCode);
  },
);

export { instance };

// ÊµèËßàÂô®ËØ∑Ê±ÇÈîôËØØÔºåÂèØËÉΩÊòØÊúçÂä°Âô®ÊàñËÄÖ404ÔºåËÆ©reactÂºÄÂèë‰∏çÊä•Èîô
const errBrowerCode = [403, 404, 500];
// ÈîôËØØÊäìÂèñ
const errHandle = (code: number | string) => {
  // console.log('err-code', code);
  // throw code;
  if (typeof code === 'string') {
    const s: any = code;
    if (isNaN(s)) {
      throw code;
    }
  }
  const c = Number(code);
  const isErrBrower = errBrowerCode.includes(c);
  if (!isErrBrower) {
    throw c;
  }
  return;
};
// Âõ†‰∏∫ÂæàÂ§öÂÜÖÂÆπÈÉΩÊòØÈÄöÁî®ÔºåÊï∞ÊçÆËØ∑Ê±Ç‰∏ÄËà¨ÈÉΩÊòØÁ±ª‰ººÁöÑÂΩ¢ÂºèÔºåËøõË°åÂØπÂ∫îÂ§ÑÁêÜÂåñÔºåÂáèÂ∞ëÈáçÂ§ç‰ª£Á†Å
const get = (url: string, requestConfig: AxiosRequestConfig | any = {}) =>
  instance.get(url, { ...requestConfig }).catch((err) => errHandle(err));
const post = (url: string, requestConfig: AxiosRequestConfig | any = {}) =>
  instance.post(url, { ...requestConfig }).catch((err) => errHandle(err));
const deleteApi = (url: string, requestConfig: AxiosRequestConfig | any = {}) =>
  instance.delete(url, { ...requestConfig }).catch((err) => errHandle(err));
const put = (url: string, requestConfig: AxiosRequestConfig | any = {}) =>
  instance.put(url, { ...requestConfig }).catch((err) => errHandle(err));
export { baseURL, get, post, deleteApi, put };
